.gpt3-feature {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
}

.gpt3-feature .title {
  flex: 1;
  max-width: 180px;
  margin-right: 2rem;
}

.gpt3-feature .title div {
  width: 38px;
  height: 3px;
  background: var(--gradient-bar);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 0.25rem;
}

.gpt3-feature .title h1 {
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.04em;
  color: #fff;
}

.gpt3-feature .text {
  flex: 2;
  max-width: 390px;
  display: flex;
}

.gpt3-feature .text p {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: var(--color-text);
}

@media screen and (max-width: 575px) {
  .gpt3-feature .title h1 {
    font-size: 16px;
    line-height: 22px;
  }

  .gpt3-feature .title p {
    font-size: 12px;
    line-height: 2px;
  }
}